import { useMemo, useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import ModalMedia from "./ModalMedia";

const CarousalPost = ({ modalData, handleAllImageError }) => {
    const [currentPost, setCurrentPost] = useState(0)

    const settings = useMemo(() => ({
        type: "slide",
        perPage: 1,
        pagination: true,
        arrows: true,
        start: 0,
    }), [])

    return (
        <div className="m_c_p m-auto">
            {modalData?.imageList?.length > 1 ? (
                <Splide hasTrack={false} options={settings} onActive={(_, prev) => setCurrentPost(prev.index)}>
                    <SplideTrack>
                        {modalData.imageList?.map((assetItem) => (
                            <SplideSlide key={assetItem.id} className={'f-center'}>
                                <ModalMedia modalData={assetItem} handleAllImageError={handleAllImageError} key={assetItem.id} />
                            </SplideSlide>
                        ))}
                    </SplideTrack>
                </Splide>
            ) : null}
        </div>
    );
};

export default CarousalPost;
