import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { connect } from "react-redux";
import EditAside from './EditAside';
import { editpost, onUpdateTags } from '../../../../../../actions/moderationActions';
import ModalMedia from '../Elements/ModalMedia';
import { FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper';
import { Link } from 'react-router-dom';
class ContentModal extends Component {
    constructor(props) {
        super(props);
        this.state = { author: '', handle: '', authorImage: '', postImage: '', content: '', tags: '', type: '', mediaUrl: '', mediaWidth: '', mediaHeight: '' }
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        const { modalData } = this.props
        this.setState({ author: modalData.name, handle: modalData.username, authorImage: modalData.picture, postImage: modalData.postFile, content: modalData.postcontent, tags: modalData.tags, type: modalData.type, loading: false, mediaUrl: modalData.mediaUrl, mediaWidth: modalData.mediaUrl, mediaHeight: modalData.mediaUrl })
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    onChangeAuthor = event => this.setState({ author: event.target.value })
    onChangeHandle = event => this.setState({ handle: event.target.value })
    onChangeAuthorImg = value => this.setState({ authorImage: value })
    onClickRemoveAuthorImg = event => this.setState({ authorImage: "" })
    onChangePostImg = value => this.setState({ postImage: value })
    onChangePostType = value => this.setState({ type: 4 })
    onChangeContent = event => this.setState({ content: event.target.value })
    onChangeTag = value => this.setState({ tags: value })

    handleResize = () => {
        const { modalData } = this.props
        this.setState({
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
        });
    };


    onClickTouploadImage = (value, callBack) => this.setState({ postImage: value }, () => callBack())



    onSavePost = event => {
        const { modalData, handleClose } = this.props
        const { authorImage, author, content, handle } = this.state;
        this.setState({ loading: true })
        const postpayload = { content: content,  wallId: localStorage.getItem('wallId'), postId: String(modalData.referenceId), postauthor: author, posthandle: handle, authorpicture: authorImage }
        this.props.editpost(postpayload, handleClose);
    }

    onSaveTag = event => {
        const { modalData, handleClose, isTaggModal } = this.props
        this.setState({ loading: true })
        const tagPayload = { postId: String(modalData.id) , feedId: String(modalData.feedId) , wallId: String(localStorage.getItem('wallId')), tags: this.state.tags };
        this.props.onUpdateTags(tagPayload, isTaggModal, handleClose)
    }


    render() {
        const { modalData, handleClose, isTaggModal, handleAllImageError } = this.props
        return <Modal size={'full'} show={true} className="assets_modal p-0" backdrop={false}>
            <Modal.Header className="justify-content-between py-0 ps-0">
                <div className='d-flex align-items-center'>
                    <Link to={``} className="btn me-3 border-end border-0 py-3 rounded-0" onClick={handleClose}>
                        <FontAwesomeIcon icon={'chevron-left p-0'} />
                    </Link>
                    <span className='text-dark fw-medium fs-5'>{isTaggModal ? 'Add Tag' : 'Edit Post'}</span>
                </div>
                <div className='d-flex align-items-center h-100'>
                    <div className='ms-5 d-flex justify-content-end'>
                        <Button variant='link' className="me-0 p-0 border-0" onClick={handleClose}>
                            <FontAwesomeIcon type={'regular'} icon={'xmark'} iconClass={'fs-5 me-1'} />
                        </Button>
                    </div>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex border-0 flex-wrap">
                <div className="assets_modal_inner flex-column flex-grow-1 f-center p-lg-4 p-xxl-5 position-relative mb-lg-0 overflow-hidden">
                    <ModalMedia modalData={modalData} handleAllImageError={handleAllImageError} />
                </div>
                <div className="full_modal_sidebar position-relative">
                    <EditAside modalData={modalData} onChangeAuthor={this.onChangeAuthor} onChangeHandle={this.onChangeHandle} onChangeAuthorImg={this.onChangeAuthorImg} onChangePostImg={this.onChangePostImg} onChangeContent={this.onChangeContent} onChangeTag={this.onChangeTag} onClickRemoveAuthorImg={this.onClickRemoveAuthorImg} handleClose={handleClose} onSavePost={this.onSavePost} onSaveTag={this.onSaveTag} isTaggModal={isTaggModal} {...this.state} />
                </div>
            </Modal.Body>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        ctaData: state.ctaData?.data,
        authorized: state.authenticated,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        editpost: (payload, callback) => dispatch(editpost(payload, callback)),
        onUpdateTags: (data, isTaggModal, callback) => dispatch(onUpdateTags(data, isTaggModal, callback))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContentModal);