import React, { Component } from 'react';
import TextType from '../../../../../CommonComponents/MediaType/TextType';
import ImageType from '../../../../../CommonComponents/MediaType/ImageType';
import VideoType from '../../../../../CommonComponents/MediaType/VideoType';
import { getImageSize } from '../../../../../../utils';


class ModalMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1,
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
            mediaSize: { height: 400, width: 400 },
        };
    }
    componentDidMount() {

        window.addEventListener('resize', this.handleResize);
        this.onMediaLoad(this.props.modalData);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    componentDidUpdate(prevProps) {
        if (this.props.modalData !== prevProps.modalData) {
            this.onMediaLoad(this.props.modalData);
        }
    }

    handleResize = () => {
        this.setState({
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
        });
    };

    onMediaLoad = async (modalData) => {
        if (modalData.mediaHeight && modalData.mediaWidth) {
            this.setState({ mediaSize: { height: modalData.mediaHeight, width: modalData.mediaWidth } })
        } else {
            try {
                const { width, height } = await getImageSize(modalData.newMedia);
                this.setState({ mediaSize: { height: height, width: width } })
            } catch (error) {
                this.setState({ mediaSize: { height: 100, width: 100 } })
            }
        }
    }
    render() {
        const { modalData, handleAllImageError,autoPlay,movePost } = this.props
        const { winWidth, winHeight, mediaSize } = this.state
        const scale = Math.min((winWidth - (winWidth > 1400 ? 850 : 650)) / mediaSize.width, (winHeight - 130) / mediaSize.height);
        const height = mediaSize.height * scale
        const width = mediaSize.width * scale
        const paddingBottom = (height * 100) / width

        return (
            modalData.type == 1 ?
                <div className='mx-auto w-100 p-4'>
                    <TextType mediaClass="bg-gray-200" size={120} content={modalData.postcontent} rating={modalData.rating} socialNetworkId={modalData.networkId} fontSize={'calc(14px + .3vw + .3vh)'} lineCount={10} />
                </div>
                :
                <div className='w-100 m-auto' style={{ maxWidth: winWidth > 991 ? width : null }}>
                    <div className="position-relative">

                        {(modalData.type == 2 || modalData.type == 4) ?
                            <ImageType mediaClass={'content_img_'} size={paddingBottom} url={modalData.postFile} height={height} width={width} icon={modalData.networkId} rating={modalData.rating} alt={''} handleAllImageError={handleAllImageError} item={modalData} isCover={true} />
                        :
                        (modalData.type == 3 || modalData.type == 5) ?
                            <VideoType key={movePost} item={modalData} mediaClass={'content_video'} size={paddingBottom} url={modalData.mediaUrl || modalData.mediaFile || modalData.mediaClip || modalData.postFile} poster={modalData.postFile} height={height} width={width} autoPlay={autoPlay ? autoPlay : true} muted={false} isCover={true} controls={true} loop={false} isupdate={1} />
                            : null}
                    </div>
                </div>
);
    }
}

export default ModalMedia;