import React, { Component } from 'react';
import { Form, OverlayTrigger, Tooltip, Button, Image, Badge } from 'react-bootstrap';
import { GET_FIRST_LETTER, TOAST_ERROR } from '../../../../../../utils';
import CreatableSelect from 'react-select/creatable';
import { FontAwesomeIcon } from '../../../../../CommonComponents/AssetsHelper';
import { autoCompleteSearch } from '../../../../../../actions/moderationActions';
import ImageUploader from "../../../../../CommonComponents/ImageUploader";

class EditAside extends Component {
       constructor(props) {
              super(props);
              this.state = { tagString: [], searchPost: '', selectedOption: [], isLoading: false, uppyProfileOpen: false, profile: '', imageError: false };
       }

       componentDidMount() {
              this.getTagsArray()
       }

       getTagsArray = () => {
              let submitTags = (this.props?.tags || "").split(",").filter(tag => tag.trim().length > 0);
              this.setState({ tagString: submitTags });

       }
       componentDidUpdate(prevProps) {
              if (prevProps.tags !== this.props.tags) {
                     this.getTagsArray();
              }
       }

       onLoad = () => {
              const { searchPost } = this.state;
              if (searchPost?.length > 0) {
                     this.setState({ isLoading: true });

                     autoCompleteSearch(searchPost).then((response) => {
                            const { responseData, responseCode } = response.data;
                            if (responseCode === 200) {
                                   const formattedTags = responseData?.tags?.map(tag => ({ value: tag, label: tag.charAt(0).toUpperCase() + tag.slice(1) }));
                                   this.setState({ selectedOption: formattedTags, isLoading: false });
                            } else {
                                   this.setState({ selectedOption: [], isLoading: false });
                            }

                     }).catch(() => {
                            this.setState({ isLoading: false });
                     });
              } else {
                     this.setState({ selectedOption: [], isLoading: false });
              }
       }


       onSearchText = (textInput) => { this.setState({ searchPost: textInput }, () => this.onLoad()); }

       handleSelectChange = (e) => {
              const { tagString } = this.state;
              if (e?.value?.length) {
                     if (tagString.includes(e.value)) {
                            TOAST_ERROR('Tag already exists');
                     } else {
                            this.setState({ tagString: [...tagString, e.value] }, () => this.props.onChangeTag(this.state.tagString.join(',')));
                     }
              }
       };

       setProfile = (imgUrl, mediaData, videoUrl) => { this.setState({ uppyProfileOpen: false},()=>this.props.onChangeAuthorImg(imgUrl)) }

       onClickToRemoveTag = (index) => event => {
              let { tagString } = this.state;
              tagString.splice(index, 1)
              this.setState({ tagString }, () => this.props.onChangeTag(tagString.join(',')))
       }

       handleSave = () => {
              const { author, handle, onSavePost, content } = this.props;
              if (author && handle && content) { onSavePost(); }
       };


       isValidNewOption = (inputValue) => {
              return inputValue.length >= 3;
       };

       render() {
              const { onChangeAuthor, onChangeHandle, onChangeContent, onClickRemoveAuthorImg, author, handle, authorImage, onSavePost, onSaveTag, content, handleClose, loading, isTaggModal } = this.props
              const { tagString, isLoading, selectedOption, imageError } = this.state
              const tagsArrayFun = () => {
                     const tagItem = tagString.map((tag, removeIndex) =>
                            <Badge key={tag.toString()} bg="" className="f-center rounded-pill btn btn-outline-secondary">
                                   <span className=' text-ellipsis d-block'>{tag}</span>
                                   <Button variant='' className="p-0 ms-1 " onClick={this.onClickToRemoveTag(removeIndex)}>
                                          <FontAwesomeIcon icon={'xmark'} iconClass={'fs-6'} />
                                   </Button>
                            </Badge>
                     );
                     return tagItem;
              }
              return (
                     <div>
                            <div className='p-4 m_sidebar_scroll_'>
                                   {!isTaggModal &&
                                          <Form.Group className='mb-3'>
                                                 <Form.Label className="mb-2" htmlFor="author_p_">Author Profile</Form.Label>
                                                 <div className="d-flex align-items-center mb-5">
                                                        <div className="me-3 me-xxl-4">
                                                               <div className="symbol h-60px w-60px position-relative rounded-circle bg-light overflow-hidden">
                                                                      {authorImage && authorImage != '' ? (
                                                                             <>
                                                                                    {!imageError ? ( <Image src={authorImage} alt={author} height={70} width={70} className="iobject-center-cover" load="lazy" fluid onError={() => this.setState({ imageError: true })} /> ) : null}
                                                                                    <span className="symbol-label bg-black text-white text-uppercase rounded-circle w-100 h-100 fs-2" style={{display: imageError ? 'flex' : 'none'}} > {GET_FIRST_LETTER(author)} </span>
                                                                             </>
                                                                      ) : ( <span className="symbol-label bg-black text-white text-uppercase rounded-circle w-100 h-100 fs-2"> {GET_FIRST_LETTER(author)} </span> )}
                                                               </div>
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column">
                                                               <div className="d-flex align-items-center">
                                                                      <OverlayTrigger placement="top" overlay={<Tooltip>Edit</Tooltip>}>
                                                                      <ImageUploader uploadPath={'media/tmpimages/'} uploadAgents={["GoogleDrive", "Url"]} allowedFile={['image/*']} onImgUpload={this.setProfile}>
                                                                         <Button variant='icon' size='sm' className="btn-active-primary m-1"> <FontAwesomeIcon type={'solid'} icon={'pen'} /> </Button>
                                                                      </ImageUploader>
                                                                      </OverlayTrigger>
                                                                      <OverlayTrigger placement="top" overlay={<Tooltip>Remove</Tooltip>}>
                                                                             <Button variant='icon' size='sm' className="btn-active-primary m-1" onClick={onClickRemoveAuthorImg}>
                                                                                    <FontAwesomeIcon type={'solid'} icon={'trash-can'} />
                                                                             </Button>
                                                                      </OverlayTrigger>
                                                               </div>
                                                               <div className="form-text mt-2">Allowed file types: png, jpg, jpeg.</div>
                                                        </div>
                                                 </div>
                                          </Form.Group>}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="author_n_">Author Name <span className='text-danger'>*</span></Form.Label>
                                          <Form.Control id="author_n_" name="author_n_" className={!author.length ? 'is-invalid' : ''} placeholder="Enter Author Name" value={author} onChange={onChangeAuthor} />
                                          {!author.length ? <span className="invalid-feedback">This field is required</span> : null}
                                   </Form.Group>}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="author_h_">Author Handle <span className='text-danger'>*</span></Form.Label>
                                          <Form.Control id="author_h_" name="author_h_" className={!handle.length ? 'is-invalid' : ''} placeholder="Enter Author Handle" value={handle} onChange={onChangeHandle} />
                                          {!handle.length ? <span className="invalid-feedback">This field is required</span> : null}
                                   </Form.Group>}
                                   {isTaggModal ? <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="tags__">Add Tag</Form.Label>
                                          <div className="mb-3">
                                                 <CreatableSelect className="selectpicker" placeholder={'Enter your Tag'} classNamePrefix="selectpicker" isClearable isLoading={isLoading} options={selectedOption} onChange={this.handleSelectChange} onInputChange={this.onSearchText} value={selectedOption} isValidNewOption={this.isValidNewOption} />
                                          </div>
                                          {/* <Button  onClick={()=>this.handleAddTagButton(searchPost)}>Add</Button> */}
                                          <div className="tags-group d-flex align-items-center flex-wrap">
                                                 {tagsArrayFun()}
                                          </div>
                                   </Form.Group> : ''}
                                   {!isTaggModal && <Form.Group className='mb-3'>
                                          <Form.Label className="mb-2" htmlFor="post_cont_">Content</Form.Label>
                                          <Form.Control as={'textarea'} className={`${!content.length ? 'is-invalid' : ''} textarea-min-row-7`} id="post_cont_" placeholder="Enter Content" value={content} onChange={onChangeContent} />
                                          {!content.length ? <span className="invalid-feedback">This field is required</span> : null}
                                   </Form.Group>}
                            </div>
                            <div className="assets_btn_wrapper px-4 py-3 f-center flex-column">
                                   {!isTaggModal ? <Button className={`w-100 mb-2 ${loading ? 'spinner' : ''}`} disabled={loading || !(author && handle && content)} onClick={this.handleSave}>Save Changes</Button>
                                          : <Button className={`w-100 mb-2 ${loading ? 'spinner' : ''}`} disabled={loading} onClick={() => onSaveTag()}>Save Changes</Button>}
                                   <Button variant='link' className='text-muted text-decoration-none' onClick={handleClose}>Cancel</Button>
                            </div>
                     </div>
              );
       }
}

export default EditAside;