import React, { Component, Suspense } from 'react';
import { Modal } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { connect } from "react-redux";
import EditPost from './EditElements';
import ModalMedia from './Elements/ModalMedia';
import { getImageSize } from '../../../../../utils';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '../../../../CommonComponents/AssetsHelper';
import CarousalPost from './Elements/CarousalPost';

const ContentModalAside = React.lazy(() => import('./Elements/ContentModalAside'));
class ContentModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            spotWidth: 0, spotHeight: 0, searchTag: '', selectedTag: [], loading: false,
            winWidth: window.innerWidth,
            winHeight: window.innerHeight,
            paddingBottom: 100,
            mediaSize: { height: 400, width: 400 },
            editPost:false,
            isTaggModal:false,
            activeCarousalPost: 0,
            activeCarousalPostsList: [],
            carousalPostsList: [],
            TagOpend:false
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
        this.onMediaLoad(this.props.modalData);
        if(this.props.editTags){this.editPostFun(true); this.setState({TagOpend:true})} 
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    componentDidUpdate(prevProps) {
        if (this.props.modalData !== prevProps.modalData) { this.onMediaLoad(this.props.modalData); } 
    }

    handleResizeg = () => {
        this.setState({ winWidth: window.innerWidth, winHeight: window.innerHeight, });
    };

    updateCarousalPost = (e, data, refId = 0, status) => {
        const getFilteredData = (list) => list.filter((obj) => obj.post_id == e);

        if (data) this.setState({ carousalPostsList: data })

        if (status) {
            const getPostData = status == 2 ? data.filter((item) => item.id != refId) : [...data];
            if (status == 1) getPostData.push(refId)
            const filteredData = getFilteredData(getPostData);
            this.setState({ carousalPostsList: getPostData, activeCarousalPostsList: [] }, () => this.updateCarousalPostList(filteredData));
        } else {
            const filteredData = getFilteredData(data || this.state.carousalPostsList);
            this.setState({ activeCarousalPost: e, activeCarousalPostsList: [], }, () => this.updateCarousalPostList(filteredData)
            );
        }
    }

    updateCarousalPostList = (e) => { this.setState({ activeCarousalPostsList: e }) }
    handleClose = () => this.props.modalCallback(false)


    onMediaLoad = async (modalData) => {
        if (modalData.mediaHeight && modalData.mediaWidth) {
            this.setState({ mediaSize: { height: modalData.mediaHeight, width: modalData.mediaWidth } })
        } else {
            try {
                const { width, height } = await getImageSize(modalData.newMedia);
                this.setState({ mediaSize: { height: height, width: width } })
            } catch (error) {
                this.setState({ mediaSize: { height: 100, width: 100 } })
            }
        }
    }
    toggleOptions = () => {
        const options = document.querySelector('.options');
        if (options) {
            if (options.classList.contains('d-none')) {
                options.classList.remove('d-none');
            } else {
                options.classList.add('d-none');
            }
        }
    }
    editPostFun = (status) => this.setState({ editPost: !this.state.editPost, isTaggModal: status })

    render() {
        const { modalToggle, modalData, filter, postPostUpdate, currentIndex, clickToPin, clickToGetUgc, clickToDeletePost, handleAllImageError, highLightPost, clickToPostStatus } = this.props
        const { winWidth, winHeight, mediaSize, editPost, isTaggModal, activeCarousalPost, activeCarousalPostsList, carousalPostsList , TagOpend } = this.state;
        const scale = Math.min((winWidth - 450) / mediaSize.width, (winHeight - 130) / mediaSize.height);
        const height = mediaSize.height * scale
        const width = mediaSize.width * scale;

        return <Modal size={'full'} show={modalToggle} className="assets_modal p-0 moderation_modal" backdrop={false}>
            <Modal.Header className="justify-content-between py-0 ps-0">
                <div className='d-flex align-items-center'>
                    <Link to={``} className="btn me-3 border-end border-0 py-3 rounded-0 moderation_back"  onClick={this.handleClose}>
                        <FontAwesomeIcon icon={'chevron-left p-0'} />
                    </Link>
                    <span className='text-dark fw-medium fs-5'>{editPost ? 'Edit Post' : 'General Info'}</span>
                </div>
                <div className='d-flex align-items-center h-100'>
                    {!editPost ?
                        <>
                            <div className="options d-none d-lg-flex">
                                {
                                    <>  {modalData?.type !==1 && modalData?.status !== 2 ?
                                        <Button variant={'link'} className={`p-0 f-center border-0 me-3 bg-transparent highlight`} onClick={() => highLightPost(currentIndex, modalData.referenceId, modalData.id, modalData.highlight, modalData)}>
                                            {modalData.highlight ? <FontAwesomeIcon type={'regular'} icon={'circle-stop'} iconClass={'fs-6 spotlight-on text-danger me-2'} />
                                                : <FontAwesomeIcon type={'solid'} icon={'telescope'} iconClass={'spotlight-off fs-6 text-secondary me-2'} />}
                                            Spotlight
                                        </Button> : null}
                                        {/* {[18, 2].includes(modalData?.networkId) ?
                                            <Button variant={'link'} className={`p-0 f-center border-0 me-3 bg-transparent refresh_post_media_Popup${modalData.id}`} onClick={() => onRefreshPost(modalData)} style={{ display: 'none' }}>
                                                <FontAwesomeIcon type={'solid'} icon={'arrows-rotate'} iconClass={'fs-6 me-2'} />
                                                Refresh Post
                                            </Button> : null} */}
                                        <Button variant={'link'} className={`p-0 f-center border-0 me-3 bg-transparent highlight ${modalData.pin ? 'text-gradient' : ''}`} onClick={() => { clickToPin(currentIndex, modalData.referenceId, modalData.pin)}}>
                                            <FontAwesomeIcon type={'solid'} icon={'thumbtack'} iconClass={'fs-6 me-2'} />
                                            Pin to Top
                                        </Button>
                                        {(((modalData.networkId == 3 && modalData.feedFilter == 62) || modalData.networkId == 7 || (modalData.networkId == 18 && (modalData.feedFilter == 24 || modalData.feedFilter == 25 || modalData.feedFilter == 26))) && modalData.ugcstatus == null && modalData.type != 1) ?
                                            <Button variant={'link'} className="p-0 f-center border-0 me-3 bg-transparent" onClick={clickToGetUgc(modalData)}>
                                                <FontAwesomeIcon type={'solid'} icon={'user-check'} iconClass={'fs-6 me-2'} />
                                                UGC Rights
                                            </Button> : null}
                                        <Button variant={'link'} className="p-0 f-center border-0 me-3 bg-transparent" onClick={() => this.editPostFun(true)}>
                                            <FontAwesomeIcon type={'solid'} icon={'tag'} iconClass={'fs-6 me-2'} />
                                            Tags
                                        </Button>
                                        <Button variant={'link'} className="p-0 f-center border-0 me-3 bg-transparent" onClick={() => this.editPostFun(false)}>
                                            <FontAwesomeIcon type={'solid'} icon={'pen'} iconClass={'fs-6 me-2'} />
                                            Edit Post
                                        </Button>
                                        {(modalData.ugcstatus != 1 || modalData.ugcstatus != 2) ?
                                            <Button variant={'link'} className="p-0 f-center border-0 me-3 bg-transparent" onClick={clickToDeletePost(modalData, modalData.id, modalData.status, currentIndex, modalData.feedId, modalData.feedFilter)}>
                                                <FontAwesomeIcon type={'solid'} icon={'trash-can'} iconClass={'fs-6 me-2'} />
                                                Delete Post
                                            </Button>
                                            : null}
                                    </>
                                }
                            </div>
                        </>
                        : null}
                    <div className='d-flex justify-content-end'>
                        <Button variant='link' className="btn-more d-lg-none me-3 p-0 border-0" onClick={this.toggleOptions}>
                            <FontAwesomeIcon type={'regular'} icon={'ellipsis-vertical'} iconClass={'p-0'} />
                        </Button>
                        <Button variant='link' className="me-2 p-0 border-0" onClick={this.handleClose}>
                            <FontAwesomeIcon type={'regular'} icon={'xmark'} iconClass={'fs-5'} />
                        </Button>
                    </div>
                </div>
            </Modal.Header>
            <Modal.Body className="p-0 d-flex border-0 flex-lg-row flex-column">
                <div className="assets_modal_inner flex-column flex-grow-1 f-center p-lg-3 p-xxl-4 position-relative mb-lg-0 overflow-hidden">

                    <Button variant='outline-secondary' className="assets_modal_arrow_left rounded-circle position-absolute box-shadow2x p-0" onClick={this.props.slideAssets(currentIndex - 1)}>
                        <FontAwesomeIcon type={'regular'} icon={'arrow-left'} />
                    </Button>

                    {modalData.highlight ? <div className='live-tag live-tag-popup mt-3'>
                        <p className='d-flex align-items-start m-0'><label className='d-inline-flex align-items-center me-2 bg-danger text-white m-0 px-2'><small className='bg-white h-8px w-8px me-2 d-inline-block rounded-circle'></small>Live</label></p>
                    </div> : null}
                    <div className='w-100 position-relative crousalPost-w-full' style={{ maxWidth: winWidth > 991 ? width : null }} key={modalData.id}>
                        {modalData.imageList?.length > 1 ? <CarousalPost {...{ modalData, handleAllImageError }} /> : <ModalMedia {...{ modalData, handleAllImageError }} />}
                    </div>
                    <Button variant='outline-secondary' className="assets_modal_arrow_right rounded-circle position-absolute box-shadow2x p-0" onClick={this.props.slideAssets(currentIndex + 1)}>
                        <FontAwesomeIcon type={'regular'} icon={'arrow-right'} />
                    </Button>
                </div>

                <div className="full_modal_sidebar position-relative">
                    {editPost ?
                        <EditPost handleClose={() =>{if(TagOpend) this.handleClose(); else this.editPostFun(false)}} modalData={modalData} filter={filter} isTaggModal={isTaggModal} handleAllImageError={handleAllImageError} />
                        :
                        <Suspense fallback={<></>}>
                            {modalData ? <ContentModalAside updateCarousalPost={this.updateCarousalPost} clickToPostStatus={clickToPostStatus} carousalPostsList={carousalPostsList} activeCarousalPostsList={activeCarousalPostsList} activeCarousalPost={activeCarousalPost} modalData={modalData} postPostUpdate={postPostUpdate} key={modalData?.id} modalCallback={this.props.modalCallback} currentIndex={currentIndex}/> : null}
                        </Suspense>
                    }
                </div>
            </Modal.Body>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        ctaData: state.ctaData?.data,
        authorized: state.authenticated,
    }
}

const mapDispatchToProps = dispatch => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContentModal);